:root{
    --app-inner-width: 80%;
    --app-background-color: white;
    --app-color:#050A30;
    --app-seondary-color:rgba(5, 10, 48, 0.7);
}
body{
    font-family: 'Times New Roman', Times, serif;
}
.divider{
    border: 1px solid rgba(128, 128, 128, 0.4);
    margin: 0px;
    padding: 0px;
    margin-inline: .4rem;
    
}

.landingPage{
    width: 1350px !important;
    font-family: 'Times New Roman', Times, serif !important;
    text-align: center;
    font-size: 2rem;
    font-family: 'Times New Roman', Times, serif;
    color: var(--app-color);
}
/* navbar css */
.navBar{
    width: 100%;
   top: 0px;
   position:fixed;
    font-weight: bold;
    color: white;
    z-index: 1;
}
.pointer{
    cursor: pointer;
}
.Home{
    outline: none;
    color: white;
    border: none;
    text-decoration: none;
}




@keyframes navbarAnimation {
    0% {opacity: 1;}
    100% {opacity: .01;}
    
}
@keyframes navbarAnimation2 {
    0% {opacity: .01;}
    100% {opacity: 1;}
    
}
.colorChange{
    
    width: 100%;
   top: 0px;
   position:fixed;
    font-weight: bold;
    color:#050A30;
    z-index: 1000;
    background-color:white;
    box-shadow: 0px 1px rgba(128, 128, 128, 0.1);
    /* opacity: 0;
    animation-name: navbarAnimation;
    animation-duration: 1.5s; */
}
/* .colorChange:hover{
    opacity: 1;
    animation-name: navbarAnimation2;
    animation-duration: 1.5s;
} */

.socialMediaIcon{
    width: 2rem;
    height: 2rem;
    padding: 2px;
}
.socialMediaIcon:hover{
    background: linear-gradient(90deg,var(--app-color),blue,white);
    border-radius: 3px;
}
.iconLinks{
    display: flex;
    justify-content: flex-end;
    
}

.innerNav{
    display: flex;
    justify-content: space-between;
    padding-inline: 5rem;
    padding-block: .5rem;
}

.pages{
    width: 30%;
    display: flex ;
    justify-content: space-between;
    margin-block: auto;
}
.logo{
   
    font-family: 'Times New Roman', Times, serif;
    margin: 0px;
    padding-inline: 1rem;
    box-shadow: 1px 1px 5px var(--app-color);
    border-radius: 0px;
    background-color: rgba(255, 255, 255, 0.5);
}
.hacker{
    margin: 0px;
    padding: 0px;
    color: white;
    font-size: 2.2rem;
    text-shadow: -1px 0 var(--app-color), 0 1px var(--app-color), 1px 0 var(--app-color), 0 -1px var(--app-color);
}
@keyframes line1Animations {
    0% {width: 100%}
    100% {width: 0%;}
    
}
@keyframes line2Animations {
    0% {width: 0%}
    100% {width: 100%;}
    
}

.Line3{
    width: 0%;
    height: 2px; 
}
.Line1{
    width: 0%;
    height: 2px;
    border-radius: 1px;
    background:linear-gradient(90deg,#050A30, steelblue, white);
    animation-name: line1Animations;
    animation-duration: 0.7s;
    margin-inline-start:auto;
    
}
.Line2{
    height: 2px;
    border-radius: 1px;
    animation-name: line2Animations;
    animation-duration: 0.7s;
    background:linear-gradient(90deg,#050A30, steelblue, white);
    margin-inline-end:auto;
}

/* Section 1 css */


.myBackgroundImage{
    width: 100%;
    
}
.flexContainer{
    width: 100%;
   
}
.flexItem1{
    position: absolute;
    width: 30%;
    font-size: 4rem;
    font-weight: bold;
    text-align: start;
    display: flex;
    color: white;
    flex-direction: column;
    justify-content: center;
    z-index: 1;
    text-shadow: 3px 3px 3px black;
    top: 35%;
    padding: .5rem;
    
}
.backgroundContainer{
    width: 100%;
    height: fit-content;
    background-color: var(--app-color);
    padding: 0px;
    margin: 0px;
    
}
.backgroundContainer>img{
    height: calc(100vh);
    opacity: .9;
    padding: 0px;
    margin: 0px;
}

.softText{
    font-size: 1.3rem;
    font-weight: normal;
    color: white;
    text-shadow: -1px 0 var(--app-color), 0 1px var(--app-color), 1px 0 var(--app-color), 0 -1px var(--app-color);
}
/* section 2 css */
.Section2{
    margin-top: 6rem;
    margin-bottom: 1rem;
    width: var(--app-inner-width);
    margin-inline: auto;
    background-color: white;
    box-shadow: 1px 1px 5px var(--app-color);
    /* border: 1px solid rgba(5, 10, 48, 1); */
}
.flexContainerSection2{
    display: flex;
    justify-content: center;
}
.mafaPic{
    width: 40%;
    background-color: black;
    padding: 0px;
    margin: 0px;
    height: fit-content;
    

    
}
.mafaPic>img{
    width: 100%;
    max-height: 30rem;
    
}

.aboutMafa{
    width: 60%;
    height: fit-content;
    padding: 0px;
    margin: 0px;
   
    
}
.aboutHeadingText{
    
    font-size: 4rem;
    font-weight: bold;
    color: white;
    background: linear-gradient(rgba(5, 10, 48, 1),rgba(0, 0, 255, 0.5),rgba(5, 10, 48, 1));
    text-shadow: -1px 0 var(--app-color), 0 1px var(--app-color), 1px 0 var(--app-color), 0 -1px var(--app-color);
    background-color: red;
}
.myName2{
    font-size: 1.1rem;
    font-weight: bold;
    
}
.infoAboutMafa{
    margin-block: 1rem;
    padding-inline: 1rem;
    color: var(--app-seondary-color);
    font-size: 1rem;
    height: fit-content;
    height: 55%;
    display:flex;
    flex-direction: column;
    justify-content: center;
  
    

}
.infoAboutMafa1{
    width: fit-content;
    height: fit-content;
    color: grey;

    
   

}
.myButton{
   
    padding: .1rem;
    margin-inline-end: auto;
    width: fit-content;
    height: fit-content;
    margin-left: 5px;
    margin-top: 1rem;
   

    
   
    
}
.myButton > button{
    width: 9rem;
    background-color: black;
    border: 2px solid black;
    height: 3rem;
    font-size: 1.2rem;
    color: white;
    font-family: 'Times New Roman', Times, serif;
    cursor: pointer;

}
.myButton1 > button{
    width: 7rem;
    background-color: black;
    border: 2px solid black;
    height: 2rem;
    font-size: 1.2rem;
    color: white;
    font-family: 'Times New Roman', Times, serif;
    margin-top: 1rem;
    cursor: pointer;

}
.myButton1{
    position: absolute;
    left: 50%;
    transform: translate(-50%);
    bottom: 5%;
}

.myButton1 > button:hover{
   background-color: white;
   color: black;
}


.myButton > button:hover{
   background-color: white;
   color: black;
}


/* secton 3 css */
.Section3{
    margin-top: 8rem;
    margin-bottom: 1rem;
    width: var(--app-inner-width);
    margin-inline: auto;
    background-color: white;
    /* border: 1px solid rgba(5, 10, 48, 1); */
    box-shadow: 1px 1px 5px var(--app-color);
}
.cardContainer{
    padding-block: 1rem;
  
}
.card{
    border: 1px solid rgba(128, 128, 128, 0.1);
    margin-inline:auto ;
   max-width: 30rem;
    height: fit-content;
    margin-inline:auto;
    display: grid;
    gap: .1rem;
    justify-content: center;
    grid-template-columns: auto auto;
    box-shadow: 1px 1px 3px grey;
   
   
}
.card>div{
    position: relative;
}
.card > img{
    
    max-height: 24rem;
}
.projectName{
    width: auto;
    font-size: 1.5rem;
    font-weight: bold;
    font-style: italic;
    box-shadow: 1px 1px 3px grey;
    margin: 5px;
}
.typeOfProject{
    
    font-size: 1.25rem;
}
.moreAboutProject{
    font-size: 1.1rem;
     margin: 5px;
    margin-top: 1rem;
    color: grey;
   
   
}

.elementContainer{
    box-sizing: border-box;
    width: 95%;
    margin-inline: auto;
}

/* section 4 css */
.Section4{
    margin-top: 8rem;
    margin-bottom: 1rem;
    width: var(--app-inner-width);
    margin-inline: auto;
    background-color: white;
    /* border: 1px solid rgba(5, 10, 48, 1); */
    box-shadow: 1px 1px 5px var(--app-color);
}

/* footer css */
.Footer{
    
    display: flex;
    justify-content: space-around;
    height: 80%;
    
    
}
.Footer > div{
    text-align: start;
     margin-block: auto;
    width: fit-content;
    height: fit-content;
}
.formContainer{
    display: grid;
    grid-template-columns: 45% auto;
    gap: 1rem;
}
.Section4Form{
    color: grey;
    display: grid;
    row-gap: 1rem;
    margin-right: 1rem;
}
.Section4Form > input {
    font-size: 1rem;
    height: 3rem;
    outline: none;
}
.Section4Form > textarea {
    font-size: 1rem;
    height: 11rem;
    outline: none;
    resize: none;
}

.formSibling{
   background-image: url("../../Assets/Images/operator.png");
   font-size: 3.5rem;
   
}

.glassEffect{
    width: 100%;
    height: 100%;
    background-color: rgba(5, 10, 48, 0.3);
    padding: .01%;
    
    

}
.glassEffect>div{
    margin-top: 1.5rem;
    width: 80%;
    height: 90%;
    margin-inline: auto;
    /* From https://css.glass */
    background: rgba(255, 255, 255, 0.2);
    border-radius: 16px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(5px);
    border: 1px solid rgba(255, 255, 255, 0.3);
    color: white;
    
}


.formButton{
    width: fit-content;
    height: fit-content;
    margin-inline-end: auto;
    margin-left: 1rem;
    margin-bottom: 1rem;
}

.formButton>button{
    font-family: 'Times New Roman', Times, serif;
    width: 9rem;
    background-color: black;
    border: 2px solid black;
    height: 3rem;
    font-size: 1.2rem;
    color: white;
    cursor: pointer;

}
.formButton>button:hover{
    background-color: white;
    border: 2px solid black;
    color: black;
}
.textContainer{
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-size: 2rem;
}
.info{
    font-size: 1rem !important;
    margin-block: 1rem;
}
.FooterContainer{
    font-family: 'Times New Roman', Times, serif;
    width: 100%;
    height: 15rem;
    background-color: #050A30;
    color: white;
    margin-top: 5rem;
    font-size: 1.2rem;
}
.copyRights{
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-size: .4rem;
    height: 18%;
}